@import "./variables";
.container {
  background-color: $light-bgColor;
  padding: 2rem;
  min-height: calc(100vh - 140px);
}

.dashHeader {
  display: flex;
  & > h2 {
    flex: 1;
  }
}
.buttonDiv {
  width: 100%;
  display: flex;
  flex-direction: column;
  border: 1px solid black;
  margin-bottom: 1.2rem;
  background: $icon-footer-color;

  align-items: center;
  &:hover {
    cursor: pointer;
    background: $icon-footer-color-dark;
  }
}
